import React from 'react'
import styled from "styled-components"

const Container = styled.div`
display: flex;
flex-direction: column;
border: 3px solid white;
color: white;
align-items: flex-start;
justify-content: center;
text-align: center;
padding: 5px;
gap: 10px;
width: 150px;
height: 80px;

div{
    width: 100%;
}

.name{
    font-size: 20px;
    text-align: center;
}
`

export default function Card({personagem}) {
  return (
    <Container>    
        <div className='name'>{personagem.nome}</div>
        <div>Categoria: {personagem.categoria}</div>
    </Container>
  )
}
